<template lang="pug">
    .test__point 
        .test__question-info
            .test__question {{index+1}}. 
            textarea(type='text' v-model='formulaQuestion' rows='3' :disabled='!testEditable').test__question-content
            vue-mathjax(:formula="formulaQuestion" v-if='texTranslateVisible').formula__question
        ul.test__answers
            li.test__answer-container(v-for="answer,idx in point.testAnswers" :key="answer.id")
                test-answer-item(
                    :answer='answer'
                    :answerChar='answerChars[idx]'
                    :texTranslateVisible='texTranslateVisible'
                    :testEditable='testEditable'
                )
            .correct
                .buttons
                    button(@click.prevent='testEditable=!testEditable' v-if='!testEditable').edit__button Редактировать
                    button(@click.prevent='testEditable=!testEditable' v-if='testEditable').save__button Сохранить
                    button(@click.prevent='testEditable=!testEditable' v-if='testEditable').remove__button Удалить
                    .latex__on(v-if='testEditable' )
                        input(type='checkbox' value='latexon' v-model='texTranslateVisible').tex__on
                        span.tex__on-text Включить трансляцию формул
                .correct__answer-content
                    .correct__text Правильный ответ: 
                    select(v-model='point.testAnswer' :disabled='!testEditable').correct__answer
                        option(:value='answer' v-for='answer in answerChars'  :selected='answer!==point.testAnswer') {{answer}}
</template>

<script>
import {VueMathjax} from 'vue-mathjax'
export default {
    components: {
        'vue-mathjax': VueMathjax,
        testAnswerItem: () => import("./test-answer-item")
    },
    props: {
        point: Object,
        index: Number,
    },
    data() {
        return {
            selected: 0,
            disabled: 0,
            texTranslateVisible: false,
            testEditable: false,
            testInput: '',
            formulaQuestion: this.point.testQuestion,
            answerChars: ['a', 'b', 'c', 'd']
        }
    },
    mounted() {
        let recaptchaScript = document.createElement('script')
        recaptchaScript.setAttribute('src', 'https://cdnjs.cloudflare.com/ajax/libs/mathjax/2.7.2/MathJax.js?config=TeX-AMS_HTML')
        document.head.appendChild(recaptchaScript)
    },
    methods: {
        checkAnswer() {
            if(this.testInput == this.point.testAnswer) {
                const local = JSON.stringify({
                    index: this.index,
                    answer: this.testInput
                })
                localStorage.setItem(this.index, local) 
            }
        }
    }
}
</script>
